import React, { useEffect } from "react";
import "./index.scss";
import { useDispatch, useSelector } from "react-redux";
import { toast } from "react-toastify";

// actions
import { getHomeProjectsApi } from "../../../stores/reducers/projectSlice";

import { CContainer, CRow, CCol } from "@coreui/react";

// components
import LoadingContainer from "../common/Loading";
import ItemHouse from "./ItemHouse";

import Img1 from "../../../assets/images/img1.webp";

const HomeContent = (props) => {
  const dispatch = useDispatch();
  const { loading, flagProjects, error } = useSelector(
    (state) => state.projects
  );

  useEffect(() => {
    dispatch(getHomeProjectsApi());
  }, [dispatch]);

  useEffect(() => {
    if (error) {
      console.log("error: ", error);
      toast.error(error);
    }
  }, [error]);

  return (
    <div className="homeContent">
      {loading ? (
        <LoadingContainer />
      ) : (
        <CContainer>
          <div className="content">
            <CRow className="rowCustom">
              {flagProjects &&
                flagProjects.length > 0 &&
                flagProjects.map((item, i) => (
                  <CCol className="pd-5" md={12} key={i}>
                    <ItemHouse project={item} image={Img1} />
                  </CCol>
                ))}
            </CRow>
          </div>
        </CContainer>
      )}
    </div>
  );
};

export default HomeContent;
